.about-block {
    display: flex;
    height: 100vh;
    position: relative;
    top: 100%;
    background-color: #DCC48E;
    color: black;
}

.column.left {
    flex: 45%;
    padding: 0 5%;
    font-family: 'Open Sans', Sans-Serif;
    font-size: 15px;
    /*color: rgb(217, 217, 217);*/
    align-self: center;
}

.big {
    font-family: 'Raleway', Sans-Serif;
    font-size: 44px;
    padding-bottom: 5px;
}

.column.right {
    flex: 55%;
    display: flex;
    flex-wrap: wrap;
    padding: 0 4px;
    height: 100%;
    justify-content: center;
    align-content: center;
}

.column.img {
    flex: 25%;
    max-width: 25%;
    padding: 0 4px;
}

.column img {
    margin-top: 8px;
    vertical-align: middle;
    max-height: 100%;
}

.fade-in.four {
    -webkit-animation-delay: 0.25s;
    -moz-animation-delay: 0.25s;/* Firefox < 16 */
    -ms-animation-delay: 0.25s;/* Internet Explorer */
    -o-animation-delay: 0.25s;/* Opera < 12.1 */
    animation-delay: 0.25s;
}

.fade-in.five {
    -webkit-animation-delay: 0.75s;
    -moz-animation-delay: 0.75s;/* Firefox < 16 */
    -ms-animation-delay: 0.75s;/* Internet Explorer */
    -o-animation-delay: 0.75s;/* Opera < 12.1 */
    animation-delay: 0.75s;
}


@keyframes fadein {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }
    to   {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
    from {
        opacity: 0;
        -webkit-transform: translateY(-20px);
    }
    to   {
        opacity: 1;
        -moz-transform: translateY(0);
    }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from {
        opacity: 0;
        -webkit-transform: translateY(-20px);
    }
    to   {
        opacity: 1;
        -webkit-transform: translateY(0);
    }
}

/* Internet Explorer */
@-ms-keyframes fadein {
    from {
        opacity: 0;
        -ms-transform: translateY(-20px);
    }
    to   {
        opacity: 1;
        -ms-transform: translateY(0);
    }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
    from {
        opacity: 0;
        -o-transform: translateY(-20px);
    }
    to   {
        opacity: 1;
        -o-transform: translateY(0);
    }
}
